.contacts {
  @include footer-border;
  min-height: 114px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 768px) {
    margin: 42px 73px;

    &__inner {
      margin: 0 71px;
      padding: 43px 0;
    }
  }

  @media (min-width: 1440px) {
    position: absolute;
    width: 570px;
    height: 201px;
    background-color: $background;
    box-sizing: border-box;
    padding: 61px 85px 51px 76px;
    left: 37px;
    top: 55px;

    &__inner {
      left: 39px;
      top: 95px;
    }
  }
}

.contacts__inner--form {
  @media (min-width: 768px) {
    margin: 43px 71px 0;
    padding: 43px 0;
  }

  @media (min-width: 1440px) {
    position: absolute;
    width: 570px;
    height: 201px;
    padding: 61px 85px 51px 76px;
    margin: 0;
    left: 110px;
    top: 95px;
  }
}

@mixin footer-border {
  width: 100%;
  margin: 0 20px;
}

.contacts__title {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 400;

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 26px;
  }

  @media (min-width: 1440px) {
    padding-bottom: 8px;
  }
}

.contacts__text {
  width: 50%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: $special-dark-gray;

  span {
    display: block;
  }

  @media (min-width: 768px) {
    font-size: 16px;
    text-align: right;
    padding-top: 5px;

    span {
      padding-top: 20px;
    }
  }

  @media (min-width: 1440px) {
    text-align: left;
    width: 39%;
    padding-top: 0;
    padding-bottom: 5px;
  }
}
