@mixin footer-border {
  margin: 0 20px;
}

.page-footer__main {
  background-color: $basic-gray;
  min-height: 150px;
}

.page-footer__contacts {
  @media (min-width: 1440px) {
    position: relative;
  }
}

.page-footer__wrapper {
  @include footer-border;
  display: grid;
  grid-template-rows: 93px 63px 79px;
  align-items: center;

  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-self: flex-end;
    margin: 0 30px;
    height: 136px;
  }

  @media (min-width: 1440px) {
    margin: 0 110px;
    min-height: 150px;
  }
}

.page-footer__name {
  align-self: center;
  margin: 0 auto;
  margin-top: 5px;

  &--inner:hover {
    opacity: 0.8;
  }

  &--inner:active {
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    margin: 0;
  }
}

.page-footer__social {
  border-top: 1px solid $light-gray-footer;

  @media (min-width: 768px) {
    border-top: none;
  }
}

.page-footer__copyright-link-block {
  display: flex;
  min-height: 80px;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid $light-gray-footer;

  @media (min-width: 768px) {
    border-top: none;
    width: 164px;
  }
}

.page-footer__contacts--inner {
  background-color: $basic-gray;
}

.page-footer__map {
  width: 100%;
  height: 362px;

  @media (min-width: 768px) {
    width: 100%;
    height: 400px;
  }

  @media (min-width: 1440px) {
    min-width: 100%;
    height: 400px;
  }
}

.page-footer__map--nojs {
  background: url("../img/location-small.jpg") no-repeat center/ cover;

  @media (min-resolution: 2dppx) {
    background: url("../img/location-small@2x.jpg") no-repeat center/ cover;
  }

  @media (min-width: 768px) {
    background: url("../img/location-medium.jpg") no-repeat center/ cover;

    @media (min-resolution: 2dppx) {
      background: url("../img/location-medium@2x.jpg") no-repeat center/ cover;
    }
  }

  @media (min-width: 1440px) {
    background: url("../img/location-large.jpg") no-repeat center/ cover;

    @media (min-resolution: 2dppx) {
      background: url("../img/location-large@2x.jpg") no-repeat center/ cover;
    }
  }
}
