.social__list {
  display: flex;
  flex-wrap: wrap;
  width: 124px;
  min-height: 63px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  list-style: none;
  padding-left: 4px;
  padding-right: 13px;

  @media (min-width: 768px) {
    width: 124px;
    padding: 0;
    margin-left: 20px;
  }
}

@media (min-width: 768px) {
  .social__link--vkontakte svg {
    width: 30px;
    height: 22px;
  }

  .social__link--instagram svg {
    width: 23px;
    height: 23px;
  }

  .social__link--facebook svg {
    width: 19px;
    height: 58px;
  }
}

@media (min-width: 768px) {
  .social__item:last-child {
    margin-right: 0;
    margin-bottom: 3px;
  }
}


.social__link-icon {
  fill: $icon-gray;

  &:hover {
    fill: $basic-green;
  }

  &--fb {
    fill: none;
    stroke: $icon-gray;

    &:hover {
      fill: none;
      stroke: $basic-green;
    }
  }
}

.social__item:active {
  opacity: 30%;
}
